import axios from "axios"

const axiosService =  axios.create({

    baseURL: process.env.VUE_APP_API_URL,
    header:{
        "Content-type":"application/json",
    }
});

function registerUser(userDTO){
    return axiosService.post('auth/signup',userDTO);
}

function checkId(uid){
    return axiosService.post('auth/checkid',uid);
}

function checkEmail(email){
    return axiosService.post('/auth/emailcheck',email);
}

function getcoinData(){
    return axiosService.get('/data');
}
function signIn(userDTO){
    return axiosService.post('/auth/signin',userDTO);
}

export { registerUser, checkId, checkEmail, getcoinData ,signIn};

